#my-account {
  .page-content {
    margin-top: rem(60px);
    margin-bottom: rem(340px);
    .link-list {
      li a {
        text-transform: uppercase;
        text-decoration: none;
        font-weight: bold;
        display: block;
        background: $gray-form;
        border-left: rem(9px) solid $green-border-form;
        border-bottom: 1px solid $gray-border-form;
        margin-bottom: rem(20px);
        padding: rem(17px);
        i {
          font-size: rem(35px);
          color: $green-dark;
          vertical-align: middle;
        }
        span {
          vertical-align: middle;
          padding-left: rem(10px);
        }
      }
    }
  }
}


