.category-cover img {
  width: 100%;
}

.subcategories > ul {
  display: flex;
  flex-wrap: wrap;
  align-items: stretch;
  justify-content: flex-start;
}

.product-miniature, .category-miniature {
  background-color: white;
}

.subcategories > ul > li {
  overflow: hidden;
  margin-bottom: 25px;
  margin-right: 25px;
  max-width: 300px;
  text-align: center;
  border: 1px solid #ccc;
  padding: 15px;
  flex-grow: 1;
  background-color: white;
}

.category-miniature img {
  max-width: 100%;
}

.category-miniature .category-description {
  text-align: left;
}

.products {
  display: flex;
  flex-wrap: wrap;
  align-items: stretch;
  justify-content: space-between;
}

@include media-breakpoint-down(sm) {
  .products, .subcategories > ul {
    justify-content: space-around;
  }
}

#index .product-miniature {
  max-width: 360px;
  flex-grow: 1;
  margin: 0 auto 25px;
}
.product-miniature {
  text-align: center;
  padding-top: 15px;
  padding-bottom: 15px;
  > div {
    border: 1px solid $gray-light;
    padding: 6px;
    &:hover {
      background-color: $gray-light;
      cursor: pointer;
      .product-thumbnail-box .product-list-actions {
        display: block;
      }
      .category-name {
        border-color: $red-light;
      }
      a.product-thumbnail {
        background: $red;
        div {
          opacity: 0.25;
        }
      }
      .pictogram_list {
        opacity: 0.5;
      }
    }
    a.product-thumbnail {
      display: block;
      div {
        background: white;
        img {
          max-width: 100%;
        }
      }
    }
    .product-thumbnail-box {
      position: relative;
      .product-list-actions {
        position: absolute;
        top: 60px;
        left: 10%;
        width: 80%;
        display: none;
        a.add-to-cart, a.know-more, a.wishlist {
          display: inline-block;
          text-decoration: none;
          color: white;
          border: 2px solid;
          padding: $extra-small-space;
          background-color: transparent;
          font-weight: bold;
          text-transform: uppercase;
        }
        a.know-more {
          font-size: rem(18px);
          width: 100%;
          margin-bottom: 9px;
        }
        a.add-to-cart {
          width: 48%;
          float: left;
        }
        a.wishlist {
          width: 48%;
          float: right;
        }
        #product-attributes-list {
          margin: rem(60px) 0 0;
          width: 100%;
          div {
            padding-left: 5px;
            padding-right: 5px;
            div {
              color: white;
              border: 1px solid;
              padding: rem(5px);
              font-size: rem(14px);
            }
          }
        }
      }
    }
    .category-name {
      color: $red;
      font-size: rem(17px);
      font-weight: 300;
      border: 2px solid $red-lighter;
      text-transform: uppercase;
      padding: 5px 0;
      margin: 10px 0 14px;
    }
    .name {
      min-height: 5rem;
      .feature-name {
        text-transform: uppercase;

      }
    }
    .product-price-and-shipping {
      font-size: rem(14px);
      padding: 17px 0 24px;
      .regular-price {
        text-decoration: line-through;
        font-weight: bold;
        font-size: rem(18px);
      }
      .price {
        font-size: rem(25px);
        font-weight: bold;
        color: $red;
      }
    }
    .product-flags li {
      position: absolute;
      top: 40px;
      left: 15px;
      text-align: left;
      &.discount-percentage {
        display: inline-block;
        padding: 8px 10px;
        color: white;
        background-color: $red;
        font-weight: bold;
        font-size: rem(22px);
      }
    }
  }
}

@include media-breakpoint-down(xs) {
  .product-miniature {
    max-width: 300px;
  }
}

.variant-links a, .variant-links a:hover {
  text-decoration: none;
  color: inherit;
}

.variant-links a.color {
  display: inline-block;
  text-indent: 100%;
  white-space: nowrap;
  overflow: hidden;
  width: 24px;
  height: 24px;
  border: 1px solid black;
}

ul.product-labels {
  margin: 0;
  padding: 0;
}

ul.product-labels li {
  list-style: none;
  display: inline-block;
  margin: $extra-small-space;
  text-transform: capitalize;
}

.product-availability.available {
  color: green;
  font-weight: bold;
}

.product-availability.unavailable {
  color: red;
  font-weight: bold;
}

.products-sort-by a, .products-sort-by a:hover {
  text-decoration: none;
  color: inherit;
}

.products-sort-by ul {
  margin: 0;
  padding: 0;
  display: inline-block;
}

.products-sort-by li {
  list-style: none;
  display: inline-block;
  padding-left: $extra-small-space;
  padding-right: $extra-small-space;
}

.product-description-short {
  text-align: left;
}

.products-sort-by li.current {
  font-weight: bold;
}

.products-sort-by a:hover {
  font-style: italic;
}

.products-sort-by li.current a:hover {
  font-style: inherit;
  cursor: default;
}

.pictogram_list {
  position: absolute;
  top: 5px;
  left: 5px;
  li {
    margin-bottom: 5px;
  }
}

#category {
  #content-wrapper {
    padding-top: 0;
    .category-cover {
      background-repeat: no-repeat;
      background-position: center center;
      background-size: cover;
      margin-bottom: rem(20px);
    }
    #category-description {
      background: rgba(181, 52, 65, 0.8);
      color: white;
      padding: rem(35px) rem(25px) rem(15px);
      margin: rem(55px) 0 rem(50px);
      .page_title {
        text-align: left;
        margin-bottom: rem(35px);
      }
    }
  }
  #footer {
    margin-top: 0;
  }
}

#category, #search {
  #products {
    #category-highlight {
      margin-top: rem(10px);
      margin-bottom: rem(30px);
      img {
        width: 100%;
      }
    }
    #js-product-list-top, #js-product-list .pagination {
      background: $gray-light;
      padding: 22px;
      text-transform: uppercase;
    }
    .js-product-list-results {
      font-weight: bold;
      font-size: rem(18px);
    }
    #js-product-list .pagination {
      margin: rem(55px) 0 rem(70px);
    }
  }
}



