#product {
  h3 {
    font-size: rem(36px);
    font-weight: lighter;
    text-align: center;
    margin-bottom: rem(45px);
  }
  #content-wrapper {
    padding-top: rem(25px);
  }
  .page-content {
    margin-top: 0;
  }
  .learn-more, .product-actions #wishlist_button_block a {
    text-transform: uppercase;
    color: $green-dark;
    font-weight: bold;
    font-size: rem(15px);
    text-decoration: none;
  }
  .product-name {
    font-size: rem(36px);
    margin-top: rem(36px);
  }
  .product-cover {
    border: 2px solid $gray-light;
    width: 100%;
    img {
      width: 100%;
    }
  }
  ul.product-images {
    width: 100%;
    padding-top: $small-space;
    overflow: auto;
    margin-left: -5px;
    margin-right: -5px;
    li {
      list-style: none;
      float: left;
      width: 20%;
      padding: 0 rem(5px);
      img {
        width: 100%;
        border: 1px solid $gray-light;
      }
    }
  }
  .images-container {
    float: left;
  }
  #product-reference, #product-condition {
    label {
      display: inline;
      font-weight: bold;
    }
    display: block;
    text-align: right;
    margin: 0;
    margin-right: 20px;
  }
  .product-information {
    #product-description-short {
      margin-bottom: rem(32px);
      line-height: rem(25px);
    }
    #product-features-pictos {
      ul {
        margin-left: rem(-5px);
        margin-right: rem(-5px);
      }
      li {
        padding: 5px;
        div {
          border: 1px solid $gray-light;
          padding: rem(10px) rem(5px) rem(8px);
          overflow: auto;
          i {
            color: $green-dark;
            float: left;
            font-size: rem(35px);
          }
          span {
            vertical-align: middle;
            text-transform: uppercase;
            font-weight: bold;
            font-size: rem(12px);
            line-height: rem(35px);
            padding-left: rem(5px);
          }
        }
      }
    }
  }
  .product-actions {
    form {
      background: $gray-form;
      border-top: 2px solid $gray-light;
      border-bottom: 2px solid $gray-light;
      .product-prices {
        border-left: 2px solid $gray-light;
        border-right: 2px solid $gray-light;
        padding: rem(20px) rem(15px);
        text-align: right;
        .product-price {
          margin-bottom: 0;
          .regular-price {
            text-decoration: line-through;
          }
          .price {
            color: $red;
            font-weight: bold;
            font-size: rem(28px);
          }
        }
      }
      .product-quantity, .product-variants {
        padding: rem(8px) rem(15px);
        background: $gray-dark;
        label {
          text-transform: uppercase;
          font-weight: bold;
          font-size: rem(12px);
        }
      }
      .product-quantity {
        border-bottom: 2px solid white;
      }
      .add-to-cart-block {
        border-left: 2px solid $gray-light;
        border-right: 2px solid $gray-light;
        padding: rem(12px);
        button {
          width: 100%;
        }
      }
      #wishlist_button_block {
        border-left: 2px solid $gray-light;
        border-right: 2px solid $gray-light;
        text-align: center;
        i {
          vertical-align: middle;
        }
      }
      .social-sharing {
        display: none;
      }
    }
    .social-sharing {
      margin-top: rem(30px);
      , a {
        color: $gray-social;
      text-transform: uppercase;
      font-weight: bold;
      }
      ul {
        display: table;
        li {
          display: table-cell;
          text-align: right;
          i {
            font-size: rem(25px);
          }
        }
      }
    }
  }
  .product-variants {
    label {
      display: inline;
    }
    > div > label + * {
      float: right;
    }
    > div {
      margin-bottom: $small-space*4;
      clear: both;
    }
  }
  .product-quantity {
    label {
      margin-bottom: 0;
    }
  }
  .product-discounts {
    clear: both;
    ul {
      margin: 0;
      padding: 0;
      list-style: none;
      li {
        //background-color: background-color;
        margin-bottom: $small-space;
        padding: $small-space;
        border-bottom: 1px solid;
      }
    }
  }
  #product-quantities, #product-availability {
    font-weight: bold;
  }
  .product-customization {
    form {
      //background-color: background-color;
      padding: $small-space;
    }
    padding-top: 0;
    textarea {
      //width: "calc(100% - 200px - %s)" % (2 * $small-space);
    }
    ul {
      margin: 0;
      padding: 0;
      list-style: none;
      li {
        margin-bottom: $small-space;
      }
      img {
        border: 1px solid;
      }
      label {
        font-weight: bold;
        width: 200px;
        display: inline-block;
        vertical-align: top;
      }
      input[type=file] {
        display: block;
        margin-left: 200px;
      }
      .remove-image {
        //remove-symbol()
        vertical-align: top;
      }
    }
  }
  .product-features {
    margin: rem(25px) 0 rem(30px);
    li .row {
      background: $gray-form;
      border-left: rem(9px) solid $green-border-form;
      border-bottom: rem(1px) solid $gray-border-form;
      margin: 0 0 rem(20px);
      padding: rem(17px);
      .product-features-name {
        text-transform: uppercase;
        font-weight: bold;
        color: $green-even-darker;
        font-size: rem(13px);
      }
      .product-features-value {
        vertical-align: middle;
        padding-left: rem(10px);
      }
    }
  }
  .product-mets-vins {
    background: url("../img/verre-vert.png") right bottom no-repeat, url("../img/couverts-verts.png") left bottom no-repeat;
    background-color: $green-lighter;
    padding-top: rem(70px);
    .block-degustation {
      background: $gray-form;
      border-left: rem(9px) solid $green-border-form;
      border-bottom: rem(1px) solid $gray-border-form;
      padding: rem(30px);
    }
    .block-mets-vins {
      padding-top: rem(45px);
      padding-bottom: rem(35px);
      background: white;
      position: relative;
      margin-top: rem(50px);
      &::before {
        background: url("../img/footer_bg_white.png") repeat-x $green-lighter;
        height: 29px;
        content: "";
        width: 100%;
        position: absolute;
        top: 0;
        left: 0;
      }
    }
  }
  #product-property, #product-calling {
    > div {
      padding: rem(18px) rem(3px);
      border: 2px solid $gray-lighter;
      line-height: rem(25px);
      img {
        border: 1px solid $gray;
        width: 100%;
      }
    }
  }
  #product-property {
    margin-top: rem(50px);
    div {
      background: $gray-form;
      a.btn-secondary {
        position: absolute;
        bottom: rem(30px);
        left: rem(15px);
      }
    }
  }
  #product-calling {
    margin-top: rem(70px);
  }
  .product-accessories {
    margin-top: rem(80px);
  }
  #footer {
    margin-top: 0;
  }
}



